// SB Content List Item Variables:
// --sb-content-list-item-border-radius
// --sb-content-list-item-box-shadow
// --sb-content-list-item-background-color
// --sb-content-list-item-border-color

.sb-content-list-item {
  background-color: var(
    --sb-content-list-item-background-color,
    var(--sbt-color-background)
  );
  border: 1px solid
    var(--sb-content-list-item-border-color, var(--sbt-theme-tertiary-color-20));
  border-radius: var(
    --sb-content-list-item-border-radius,
    var(--sbt-border-radius-large)
  );
  box-shadow: var(--sb-content-list-item-box-shadow, var(--sbt-elevation-20));
  display: flex;
  margin: 0.5rem;

  min-height: 3rem;
  transition: 0.15s cubic-bezier(0.2, 0, 0.4, 0.9);
}

.sb-content-list-item__reorder {
  background-color: var(--sbt-theme-tertiary-color-10);
  border-radius: var(--sbt-border-radius-large) 0 0
    var(--sbt-border-radius-large);
  cursor: grab;
  display: flex;
  width: 1.25rem;
}

.sb-content-list-item__reorder-disabled {
  background-color: var(--sbt-color-background);
  cursor: default;
}

.sb-content-list-item__reorder-icon {
  color: var(--sbt-theme-tertiary-color-20);
  margin: auto;
}

.sb-content-list-item__reorder-icon-disabled {
  color: var(--sbt-theme-tertiary-color-10);
}

.sb-content-list-item__content {
  display: block;
  padding: 0.75rem;
  width: 100%;
}

.sb-content-list-item__interactive {
  &:hover {
    border-color: var(--sbt-theme-color-30);
    box-shadow: var(--sbt-elevation-40);
  }
}

.sb-content-list-item__dragging {
  box-shadow: var(--sbt-elevation-20);
}
